.soc-block {
  padding-bottom: 16px;
  border-top: 1px solid hsla(0,0%,59.2%,.25);
  border-right-style: none;
  padding-top: 5%;
  text-align: center;
}

.w-list-unstyled {
  padding-left: 0;
  list-style: none;
}

.social-list {
  display: flex;
  justify-content: center;
  margin-bottom: 5%;
}

.social-item {
    padding-right: 10px;
    padding-left: 10px;
}

.w-inline-block {
  max-width: 100%;
  display: inline-block;
}
